.collapseAll {
  color: #fff;
  font-weight: 600;
  cursor: pointer;

  .collapseName {
    margin: 0;
    font-size: 18px;
    padding: 20px 32px;
    overflow: hidden;
  }
  .collapseValue {
    transition: all 0.5s;
    max-height: 0;
    overflow: hidden;
    p {
      padding: 20px 50px;
    }
    p:hover{
      color: #ffc107;
    }
  }
  a {
    width: 100%;
    color:#111;
  }
}

.collapseAll.active,.collapseAll:hover {
  background-color: #333;
  .collapseValue {
    max-height: 400px;
    p {
      border-top: 1px solid rgba(0, 0, 0, 0.4);
    }
    p:hover{
      color: #fff;
    }
  }
  a{
    color: #fff;
  }
  a.textActive{
    color: #A9540F;
  }
  
  a.menuLeftTitle{
    color: #fff;
  }
  .collapseValue:hover{
    color: #fff;
  }
}

section {
  .section-left {
    background-color: #ccc;
    //background-color: #fcf1e7;
    padding: 0;
    .title {
      font-size: 22px;
      color: #fff;
    }
  }
}