.SearchAll{
    display:flex;
    width:100%;
    flex-wrap: wrap;
    &__title{
        display:flex;
    }
    &__date{
        display:flex;
        align-items: center;
        i{
            font-size:20px;
            margin-right:6px;
        }
    }
    &__searchBtn{
        display:flex;
        justify-content: flex-end;
        text-align: right;
    }
}