.top-50 {
  transform: translateY(-50%);
  top: 50%;
}

.left-50 {
  transform: translateX(-50%);
  left: 50%;
}

.position-center {
  transform: translateY(-50%) translateX(-50%);
  top: 50%;
  left: 50%;
}

.right-10 {
  right: 10px;
}

.m-lf-15 {
  margin-left: -15px;
  margin-right: -15px;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-bottom: 0;
}

input,
select {
  display: block;
  height: calc(1.5em + 0.75rem + 2px);
  padding: 0.3rem 0.5rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.hide {
  display: none !important;
}

.show {
  display: inline-block;
}

input.largeInput,
select.largeInput {
  width: 140px;
}

input.smallInput {
  width: 100px;
}

textarea {
  // width: calc(100% - 124px);
  height: 50px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

button.btnBrownFull {
  background-color: #9a5e0a;
  color: #fff;
}

button.btnBrownFull:hover {
  background-color: #9a5e0a;
  color: #fff;
}

button.btnBrown {
  border: 1px solid #9a5e0a;
  color: #9a5e0a;
}

button.btnBrown:hover {
  border: 1px solid #9a5e0a;
  color: #9a5e0a;
}

button.btnGray {
  border: 1px solid #555;
  color: #555;
}

.tableBorder79 {
  word-break: break-all;

  .thead {
    background-color: #ca9569;
    color: #fff;
    text-align: center;
  }
  .tbody {
    div {
      border: 1px solid #797979;
      min-height: 20px;
    }
  }
}

.pBrown {
  color: #9a5e0a;
  text-decoration: underline;
}

.pBlue{
  color: blue;
  text-decoration: underline;
}

.userCircle {
  color: #222;
  border-radius: 50%;

  width: 102px;
  height: 55px;
  overflow: hidden;
  p {
    line-height: 55px;
    text-align: center;
  }
}

.nav-right {
  div {
    margin-right: 5px;
  }
}
.msg {
  position: relative;
  p {
    position: absolute;
    right: -5px;
    top: -5px;
    background-color: red;
    color: #fff;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    line-height: 20px;
    text-align: center;
  }
}

section {
  .section-right {
    display: flex;
    flex-wrap: wrap;
    padding: 0;
  }
}

a {
  color: #0000ff;
  display: inline-block;
  width: 100%;
}

label {
  display: flex;
  align-items: center;
  margin: 0 5px 0 5px;
}

a.brownType {
  color: #9a5e0a;
  text-decoration: underline;
  width: auto;
}

a.whiteType {
  color: #fff;
  width: auto;
}

a.whiteType:hover {
  color: #fff;
  text-decoration: unset;
}

.background-brown{
  background-color: #9a5e0a!important;
}

p.brownType{
  color: #9a5e0a;
  text-decoration: underline;
  width: auto;
  cursor: pointer;
}

span.brownType{
  color: #9a5e0a;
  cursor: pointer;
  text-decoration: underline;
}

.brownType{
  color: #9a5e0a!important;
  cursor: pointer;
  text-decoration: underline!important;
}

.urbank-line-black{
  width: 2px;
  height: 28px;
  margin: 0 10px;
  border: 1px solid #222;
  display: block;
}

.urbank-display-all-center{
  display: flex;
  align-items: center;
  justify-content: center;
}
