$color0: #ca956a;
$color1: #bf7f4c;
$color2: #b46a2d;
$color3: #a9540f;
$color4: #8d3f00;

.dashboardPuzzle {
  margin-bottom: 30px;
  display: flex;
  flex-wrap: wrap;

  &__top {
    border-radius: 8px;
    background-color: #ca956a;
    color: #fff;
    padding: 0;
    font-weight: 600;
    margin-bottom: 20px;
    &__title {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 18px 0;
      font-size: 20px;
      border-bottom: 2px solid #fff;
      i {
        font-size: 30px;
        margin-right: 10px;
      }
    }
    &__new {
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 16px;
      padding: 28px 0;
      p {
        span {
          font-size: 30px;
          margin: 0 10px;
        }
      }
    }
  }

  &__value:nth-child(odd) {
    padding-left: 10px;
    padding-right: 0px;
  }

  &__value:nth-child(even) {
    padding-left: 0px;
    padding-right: 10px;
  }

  &__value {
    margin-bottom: 20px;
    // padding: 0;
    border-radius: 8px;
    font-weight: 600;
    &__title {
      display: flex;
      align-items: center;
      i {
        font-size: 40px;
        margin-right: 20px;
      }
      p {
        font-size: 20px;
      }
    }

    &__new {
      color: #fff;
      padding: 30px 15px;
      text-align: center;
      margin-top: 10px;
      background-color: #bf7f4c;
      border-radius: 8px 8px 0 0;
      p {
        span {
          margin: 0 10px;
          font-size: 30px;
        }
      }
    }
    &__other {
      display: flex;
      justify-content: space-between;
      background-color: #bf7f4c;
      border-radius: 0 0 8px 8px;
      border-top: 1px solid #fff;
      &__value {
        width: 49%;
        text-align: center;

        color: #fff;
        padding: 10px 0;
        
        p {
          span {
            margin: 0 10px;
            font-size: 30px;
          }
        }
      }
      &__value:nth-child(odd){
        border-right: 1px solid #fff;
      }
    }
  }
}

.dashboardPuzzle {
  &__top {
    background-color: $color0;
  }
  &__value.styleOne {
    .dashboardPuzzle__value__title {
      color: $color1;
    }
    .dashboardPuzzle__value__new,
    .dashboardPuzzle__value__other {
      background-color: $color1;
    }
  }
  &__value.styleTwo {
    .dashboardPuzzle__value__title {
      color: $color2;
    }
    .dashboardPuzzle__value__new,
    .dashboardPuzzle__value__other {
      background-color: $color2;
    }
  }
  &__value.styleThree {
    .dashboardPuzzle__value__title {
      color: $color3;
    }
    .dashboardPuzzle__value__new,
    .dashboardPuzzle__value__other {
      background-color: $color3;
    }
  }
  &__value.styleFour {
    .dashboardPuzzle__value__title {
      color: $color4;
    }
    .dashboardPuzzle__value__new,
    .dashboardPuzzle__value__other {
      background-color: $color4;
    }
  }
}
